<template>
	<view class="inspection">
		<view class="subsection"><u-subsection :list="list" :current="1" mode="subsection" active-color="#cc300d" height="100"></u-subsection></view>
		<view v-if="!ProjectStatus">
			<u-row gutter="16">
				<u-col span="4"><u-button type="success" @click="addProject">新增</u-button></u-col>
				<u-col span="4"><u-button type="error" @click="Project('delete')">删除</u-button></u-col>
				<u-col span="3"><u-button type="success" @click="Project('share')">分享</u-button></u-col>
			</u-row>
		</view>
		<view v-else>
			<u-row gutter="16">
				<u-col span="4"><u-button type="error" @click="deleteProjectOn">确定{{status=='delete'?'删除':'分享'}}</u-button></u-col>
				<u-col span="4"><u-button type="success" @click="deleteProjectOff">取消{{status=='delete'?'删除':'分享'}}</u-button></u-col>
			</u-row>
		</view>
		<view class="search"><u-search placeholder="请输入项目名称或地址" v-model="keyword" @custom="searchChange"></u-search></view>
		<view class="text">共有X条记录</view>
		<scroll-view class="content" :style="{ height: navHeight + 'px' }" scroll-y="true">
			<view class="box" @click="projectDetails" v-if="!ProjectStatus">
				<!-- <u-checkbox-group @change="checkboxGroupChange">
					<u-checkbox @change="checkboxChange" v-model="item.checked" :name="item.name"> -->
				<u-row gutter="16">
					<u-col span="3"><image src="../../static/communication.png" mode=""></image></u-col>
					<u-col span="8">
						<ul>
							<li>项目名称:XXXX</li>
							<li>项目位置:XXXX</li>
							<li>项目设备数量:XXXX</li>
						</ul>
					</u-col>
				</u-row>
				<!-- </u-checkbox>
				</u-checkbox-group> -->
			</view>
			<!-- 删除项目 -->
			<view class="deleteProject" v-else>
				<u-checkbox-group @change="checkboxGroupChange" wrap="true" size="50" icon-size="30" width="500rpx">
					<u-checkbox @change="checkboxChange" v-model="item.checked" v-for="(item, index) in list2" :key="index" :name="item.name">
						<view class="box">
							<u-row gutter="16">
								<u-col span="3"><image src="../../static/communication.png" mode=""></image></u-col>
								<u-col span="8">
									<ul>
										<li>项目名称:XXXX</li>
										<li>项目位置:XXXX</li>
										<li>项目设备数量:XXXX</li>
									</ul>
								</u-col>
							</u-row>
						</view>
					</u-checkbox>
				</u-checkbox-group>
			</view>
		</scroll-view>
		<!-- <view class="empty">
			<u-empty text="暂无历史数据" iconSize="300"></u-empty>
		</view> -->
	</view>
</template>

<script>
export default {
	data() {
		return {
			list: [
				{
					name: '自建项目'
				},
				{
					name: '代管项目'
				}
			],
			list2: [
				{
					name: 'apple',
					checked: false,
					disabled: false
				},
				{
					name: 'banner',
					checked: false,
					disabled: false
				},
				{
					name: 'orange',
					checked: false,
					disabled: false
				}
			],
			keyword: '',
			current: 1,
			pH: 0, //窗口高度
			navHeight: 0, //元素的所需高度
			ProjectStatus: false,
			status:''
		};
	},
	mounted() {
		let that = this;
		uni.getSystemInfo({
			//调用uni-app接口获取屏幕高度
			success(res) {
				//成功回调函数
				//console.log(res);
				that._data.pH = res.windowHeight; //windoHeight为窗口高度，主要使用的是这个
				console.log(that._data, 665);
				let titleH = uni.createSelectorQuery().select('.content'); //想要获取高度的元素名（class/id）

				titleH
					.boundingClientRect(data => {
						console.log(data);
						let pH = that._data.pH;

						that._data.navHeight = pH - data.top - 10; //计算高度：元素高度=窗口高度-元素距离顶部的距离（data.top）
						console.log(that._data, 66);
					})
					.exec();
				// 			console.log(that._data,999)
			}
		});
	},
	methods: {
		addProject(){
			uni.navigateTo({
				url:"/myPagesA/pages/addProject"
			})
		},
		
		//删除确定
		deleteProjectOn(){
			this.ProjectStatus = false;
		},
		//删除取消
		deleteProjectOff(){
			this.ProjectStatus = false;
		},
		//点击删除,//点击分享
		Project(status) {
			this.status = status
			this.ProjectStatus = true;
		},
		// 选中某个复选框时，由checkbox时触发
		checkboxChange(e) {
			//console.log(e);
		},
		// 选中任一checkbox时，由checkbox-group触发
		checkboxGroupChange(e) {
			// console.log(e);
		},
		// 全选
		checkedAll() {
			this.list.map(val => {
				val.checked = true;
			});
		},
		searchChange(e) {
			console.log(2222);
		},
		//项目详情
		projectDetails() {
			uni.navigateTo({
				url: '/myPagesA/pages/projectDetails'
			});
		}
	}
};
</script>

<style lang="scss" scoped>
.inspection {
	
	.subsection {
		border: 1px solid #cc300d;
		margin-bottom: 10px;
	}
	.text {
		text-align: center;
		// background-color: #eae9e8;
	}
	.search {
		margin-top: 10px;
	}
	.content {
		background-color: #f5f5f1;
		// border-radius: 10px;
		// width: 95%;
		padding-top: 10px;
		.box {
			width: 100%;
			height: 105px;
			background-color: #fff;
			border-radius: 20px;
			margin-bottom: 10px;
			// position: relative;
			image {
				width: 100px;
				height: 70px;
				// position: absolute;
				// top: 50%;
				margin-top: 15px;
			}
			ul {
				li {
					margin-top: 10px;
				}
			}
		}
		.deleteProject {
			.box {
				width: 145%;
				height: 105px;
				background-color: #fff;
				border-radius: 20px;
				margin-bottom: 10px;
				// position: relative;
				image {
					width: 100px;
					height: 70px;
					// position: absolute;
					// top: 50%;
					margin-top: 15px;
				}
				ul {
					li {
						margin-top: 10px;
					}
				}
			}
		}
	}

	.empty {
		margin-top: 100px;
	}
}
</style>
